import React from 'react'

class CardCover extends React.Component{

    static defaultProps = {
        avatar: "",
        name: "",
      }

      constructor(props) {
        super(props);
      }

      onSummit = () =>{

      }
      
    render(){
        return(
            <div className="warp-cover">
                <div className="warp-text">
                    <h1>ฐานข้อมูลผู้สูงอายุ</h1>
                    <button 
                        type="button" 
                        className="btn"
                        onClick = {this.onSummit}
                    >
                        <h6>เข้าสู่หน้าหลัก</h6></button>
                </div>
                <div className="cover">
                    <img src={this.props.avatar} alt={this.props.cover}/>
                </div>

            </div>
        )
    }
} 
export default CardCover;